import React, { Component } from 'react';
import M from 'materialize-css/dist/js/materialize.min.js';
import { Navbar as Nav, NavItem, Icon } from 'react-materialize';

export class Navbar extends Component {
  // Activate SideNav behavior
  componentDidMount() {
    var elem = document.querySelector('.sidenav');
    var instance = M.Sidenav.init(elem, {
      edge: 'left',
      inDuration: 250,
    });
  }

  render() {
    return (
      <>
        <header>
          <Nav
            alignLinks='right'
            brand={
              <a className='brand-logo center' href='/'>
                <span style={{ color: 'green' }}>Fake</span>
                <span style={{ color: 'Red' }}>Trade</span>
              </a>
            }
            id='mobile-nav'
            menuIcon={<Icon>menu</Icon>}
            options={{
              draggable: true,
              edge: 'left',
              inDuration: 250,
              onCloseEnd: null,
              onCloseStart: null,
              onOpenEnd: null,
              onOpenStart: null,
              outDuration: 200,
              preventScrolling: true,
            }}
            sidenav={
              <ul>
                <li><a href='/login'>Login</a></li>
                <li><a href='/register'>Sign Up</a></li>
              </ul>
            }
          >
            <NavItem href='/login'>Login</NavItem>
            <NavItem href='/register'>Sign Up</NavItem>
          </Nav>
          ;
        </header>
      </>
    );
  }
}
